<template>
  <div ref="echartContainer" style="width: 100%; height: 100px;"></div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: 'EChartAnimation',
  mounted() {
    this.initEChart();
  },
  methods: {
    initEChart() {
      const option = {
        graphic: {
          elements: [
            {
              type: 'text',
              left: 'center',
              top: 'center',
              style: {
                text: 'My To-Do List',
                fontSize: 50,
                fontWeight: 'bold',
                lineDash: [0, 200],
                lineDashOffset: 0,
                fill: 'transparent',
                stroke: '#000',
                lineWidth: 1
              },
              keyframeAnimation: {
                duration: 3000,
                loop: true,
                keyframes: [
                  {
                    percent: 0.7,
                    style: {
                      fill: 'transparent',
                      lineDashOffset: 200,
                      lineDash: [200, 0]
                    }
                  },
                  {
                    // Stop for a while.
                    percent: 0.8,
                    style: {
                      fill: 'transparent'
                    }
                  },
                  {
                    percent: 1,
                    style: {
                      fill: 'black'
                    }
                  }
                ]
              }
            }
          ]
        }
      };

      const chart = echarts.init(this.$refs.echartContainer);
      chart.setOption(option);
    }
  }
};
</script>