<template>
    <div ref="echartContainer" style="width: 50%; height: 80px;"></div>
</template>
  
<script>
  import * as echarts from 'echarts';
  
  export default {
    name: 'EChart',
    mounted() {
      this.initEChart();
    },
    methods: {
      initEChart() {
        const option = {
          graphic: {
            elements: [
              {
                type: 'group',
                left: 'center',
                top: 'center',
                children: new Array(7).fill(0).map((val, i) => ({
                  type: 'rect',
                  x: i * 20,
                  shape: {
                    x: 0,
                    y: -40,
                    width: 10,
                    height: 80
                  },
                  style: {
                    fill: '#303133'
                  },
                  keyframeAnimation: {
                    duration: 1000,
                    delay: i * 200,
                    loop: true,
                    keyframes: [
                      {
                        percent: 0.5,
                        scaleY: 0.3,
                        easing: 'cubicIn'
                      },
                      {
                        percent: 1,
                        scaleY: 1,
                        easing: 'cubicOut'
                      }
                    ]
                  }
                }))
              }
            ]
          }
        };
  
        const chart = echarts.init(this.$refs.echartContainer);
        chart.setOption(option);
      }
    }
  };
  </script>
  
  <style scoped>
  /* 你的样式 */
</style>